body {
  font-size: 26px;
  line-height: 1.4;
  font-family: Courier New, Courier, monospace;
  background-color: #000;
  color: #fff;
}
p {
  margin-bottom: 25px;
}
ul {
  margin-bottom: 25px;
}
ul li + li {
  margin-top: 5px;
}
a {
  display: inline;
  position: relative;
}
a::before,
a::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: height 0.3s ease;
  z-index: -1;
}
a::after {
  z-index: -2;
  background-image: url("../images/rainbow-static.webp");
  opacity: 0.4;
}
a:hover {
  color: #000;
}
a:hover::before {
  z-index: -2;
  height: 100%;
}
a:hover::after {
  z-index: -1;
  height: 100%;
}
a[href*='last.fm'] {
  color: inherit;
}
a[href*='last.fm']::before {
  background-color: #b20000;
}
a[href*='last.fm']::after {
  opacity: 0.2;
}
a[href*='linkedin'] {
  color: inherit;
}
a[href*='linkedin']::before {
  background-color: #0077b5;
}
a[href*='linkedin']::after {
  opacity: 0.2;
}
a[href*='instagram.com'] {
  color: inherit;
}
a[href*='instagram.com']::before {
  background-image: radial-gradient(circle at 33% 100%, #fed373 4%, #f15245 30%, #d92e7f 62%, #9b36b7 85%, #515ecf);
}
a[href*='instagram.com']:hover {
  color: #fff;
}
a[href*='instagram.com']::after {
  opacity: 0.2;
}
.page-content {
  padding: 50px;
  animation: glitch 1s linear infinite;
}
.page-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -3;
  background-image: url("../images/static.gif");
  background-size: cover;
  opacity: 0.05;
}
.page-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: linear-gradient(to bottom, rgba(255,0,0,0) 0%, #fffafa 50%, rgba(255,255,255,0.98) 51%, rgba(255,0,0,0) 100%);
  opacity: 0.8;
  animation: vline 3s linear infinite;
  will-change: top;
}
@-moz-keyframes vline {
  0% {
    top: 0px;
  }
  100% {
    top: 100%;
  }
}
@-webkit-keyframes vline {
  0% {
    top: 0px;
  }
  100% {
    top: 100%;
  }
}
@-o-keyframes vline {
  0% {
    top: 0px;
  }
  100% {
    top: 100%;
  }
}
@keyframes vline {
  0% {
    top: 0px;
  }
  100% {
    top: 100%;
  }
}
@-moz-keyframes glitch {
  0% {
    transform: scale(1, 1.002);
  }
  50% {
    transform: scale(1, 1.0001);
  }
  100% {
    transform: scale(1.001, 1);
  }
}
@-webkit-keyframes glitch {
  0% {
    transform: scale(1, 1.002);
  }
  50% {
    transform: scale(1, 1.0001);
  }
  100% {
    transform: scale(1.001, 1);
  }
}
@-o-keyframes glitch {
  0% {
    transform: scale(1, 1.002);
  }
  50% {
    transform: scale(1, 1.0001);
  }
  100% {
    transform: scale(1.001, 1);
  }
}
@keyframes glitch {
  0% {
    transform: scale(1, 1.002);
  }
  50% {
    transform: scale(1, 1.0001);
  }
  100% {
    transform: scale(1.001, 1);
  }
}
